.App {
  width: 100vw;
  height: 100vh;
  
  
}

body{
   margin: 0;
   padding: 0;
}

.Sidebar{
  height: 100vh;
  margin-top: -16px;
  width:250px;
  /* background-color: #2F4050; */
  background-color: #142d52;

}

.SidebarList{
  height: 100%;
  width: 100%;
  padding: 0;
}

.SidebarList .row{
  width: 70%;
  height: 50px;
  font-size: small;
  list-style-type: none;
  margin: 0;
  display: flex;
  flex-direction: row;
  color: white;
  justify-content: center;
  align-items: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.SidebarList .row:hover{
  cursor: pointer;
  /* background-color: #293846; */
  background-color: #11192a;

}

.row #icon{
  flex: 10%;
  display: grid;
  place-items: center;
}

.row #title{
  flex: 40%;
}

